<template>
  <div class="swiperLists">
    <swiper class="swiperList0" :options='swiperOptions1' ref="swiperBg" v-if="list.length > 0">
      <swiper-slide class="swipeSlide" v-for="(item, index) in list" :key="index">
        <img :src="$utils.exceptWebp(item.cover, 1)" alt="">
        <div class="back-mask"></div>
        <div class="white-cover"></div>
      </swiper-slide>
    </swiper>
    <swiper class="swiperList1" :options="swiperOptions" v-if="list.length > 0" ref="swiperMain">
      <swiper-slide class="swiper-slide" :data-index="index" v-for="(item, index) in list" :key="index">
        <div class="content">
          <div class="content-top flex-row">
            <div class="content-top-left">
              <div class="date">{{$utils.timestampToStr(item.release_time * 1000, 'Y.M')}}</div>
              <div class="date-time">{{$utils.timestampToStr(item.release_time * 1000, 'D')}}</div>
              <div class="remarks">正在上映</div>
              <div class="c-grade">{{item.rate}}分</div>
              <div class="grade">
                <div class="grade-icon">
                  <div class="grade_progress" :style="{width: `${item.rate * 10}%`}"></div>
                </div>
                <div class="acturally">{{item.grade}}</div>
              </div>
            </div>
            <div class="content-top-right">
              <div class="video_cover">
                <img :src="$utils.exceptWebp(item.cover, 1)" alt="轮播图">
                <div class="title">《{{$utils.splitStr(item.title, 7)}}》</div>
              </div>
            </div>
          </div>
          <div class="flex-row title-flow jusB">
            <div class="flex-row left-row">
              <div class="duration" v-if="item.duration">{{item.duration ? item.duration + '分钟' : '暂无时长'}}</div>
              <div class="svgRect"></div>
              <div class="duration">{{item.location}}</div>
            </div>
            <div class="button">立即播放</div>
          </div>
          <div class="leader">导演&nbsp;{{item.director}}</div>
          <div class="actions">主演&nbsp;{{item.actor}}</div>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  props: {
    list: {
      type: Array
    },
    isRefresh: {
      type: Number,
      default: 0
    }
  },
  computed: {
    swiperBg () {
      return this.$refs.swiperBg.swiper
    },
    swiperMain () {
      return this.$refs.swiperMain.swiper
    }
  },
  data () {
    let that = this
    return {
      realIndex: 1,
      swiperOptions: {
        slidesPerView: 'auto',
        loop: true,
        watchSlidesProgress: true,
        spaceBetween: 25,
        slidesOffsetBefore: 30,
        slidesOffsetAfter: 80,
        clickable: true,
        controller: {
          control: that.swiperBg
        },
        on: {
          progress: function (progress) {
            for (let i = 0; i < this.slides.length; i++) {
              let prevIndent
              let slide = this.slides.eq(i)
              let slideProgress = this.slides[i].progress
              let scale
              if (i === 4) {
                prevIndent = 0.3228
              } else {
                prevIndent = 0.0898
              }
              if (Math.abs(slideProgress + prevIndent) < 1) {
                scale = (1 - Math.abs(slideProgress + prevIndent)) * 0.1 + 1
              } else {
                scale = 1
              }
              slide.find('.content').transform('scale3d(' + scale + ',' + scale + ',1)')
            }
          },
          setTransition: function (transition) {
            for (var i = 0; i < this.slides.length; i++) {
              var slide = this.slides.eq(i)
              slide.find('.content').transition(transition)
            }
          },
          // 处理左侧拖入时背景图消失
          touchMove: function () {
            if (this.progress < 0.01) {
              // this.controller.control = ''
            } else {
              // this.controller.control = that.swiperBg
              let index
              let diff = this.activeIndex - this.previousIndex
              if (diff === 1) {
                index = this.activeIndex + 1
              } else if (diff === -1) {
                index = this.activeIndex - 1
              } else if (diff > 2) {
                index = this.activeIndex + 1
              } else if (diff < -2) {
                index = this.activeIndex - 1
              }
              index = this.slides[index].getAttribute('data-index')
              that.swiperBg.slideTo(index, 0)
            }
          },
          click () {
            let index = this.slides[this.clickedIndex].getAttribute('data-index')
            that.toSearch(index)
          },
          transitionEnd () {
            let index = this.slides[this.activeIndex].getAttribute('data-index')
            that.swiperBg.slideTo(index, 0)
          }
        }
      },
      swiperOptions1: {
        effect: 'fade',
        fadeEffect: {
          crossFade: false
        },
        on: {
          transitionEnd () {
            
          }
        }
      }
    }
  },
  watch: {
    isRefresh () {
      this.swiperMain.slideTo(0, 0)
      this.swiperBg.slideTo(0, 0)
    }
  },
  methods: {
    toSearch (index) {
      this.$emit('toSearch', {
        item: this.list[index],
        movieType: 1
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.swiperLists {
  height: 600px;
  margin-top: -1px;
  width: 100%;
  position: relative;
  .swiperList0 {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    .swipeSlide {
      height: 700px;
      // position: relative;
      .back-mask {
        position: absolute;
        left:0;
        right: 0;
        top: 0;
        bottom: 0;
        background: rgba($color: #000, $alpha: .5)
      }
      img {
        width: 100%;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        display: block;
      }
      .white-cover {
        position: absolute;
        background: url('../../assets/img/movie/arrow.png') no-repeat left top;
        background-size: 100%;
        bottom: 0;
        left: 0;
        right: 0;
        height: 260px;
      }
    }
  }
  .swiperList1 {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: 700px;
    z-index: 2;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding-top: 70px;
    .swiper-slide {
      width: 433px;
      height: 534px;
      .content {
        width: 433px;
        height: 564px;
        border-radius: 10px;
        background: #FAFAFA;
        box-shadow: 0 4px 6px 0 #C1C1C1;
        border-radius: 10px;
        background: #fafafa;
        .content-top {
          border-bottom: 0.5px solid #e5e5e5;
          background: #FAFAFA;
        }
        .content-top-left {
          flex: 1;
          min-width: 0;
          align-self: flex-start;
          .date {
            padding: 29.2px 10px 1px;
            font-size: 23.7px;
            font-weight: 900;
          }
          .date-time {
            font-size: 54.7px;
            font-weight: 900;
            line-height: 76.6px;
          }
          .remarks {
            font-size: 24.6px;
            color: #666;
            line-height: 34.6px;
            padding: 0;
          }
          .c-grade {
            font-size: 45.6px;
            margin-top: 77px;
            margin-bottom: 4px;
            color: #F4A82B;
          }
          .grade {
            transform: scale()
          }
        
        }
        .title-flow {
          padding: 10px 18px;
          font-size: 27px;
          white-space: nowrap;
          .left-row {
            align-items: center;
            .svgRect {
              width: 6px;
              margin: 0 6px 4px;
              height: 6px;
              background: #999;
              border-radius: 6px;
              min-width: 6px;
            }
          }
          .button {
            border-radius: 22.5px;
            height: 45px;
            background-image: linear-gradient(-90deg, #F6CB2F 0%, #F39C6B 100%);
            box-sizing: border-box;
            font-size: 24px;
            width: 152px;
            line-height: 50px;
            color: #fff;
            text-align: center
          }
        }
        .leader {
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          margin-bottom: 0px;
        }
        .leader, .actions {
          line-height: 30px;
          font-size: 21.8px;
          color: #666;
          padding: 0 18px;
        }
        .actions {
          max-height: 58px;
          overflow: hidden;
          text-overflow: -o-ellipsis-lastline;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          /* autoprefixer: off */
          -webkit-box-orient: vertical;
          /* autoprefixer: on */
        }
        .content-top-right {
          width: 288px;
          height: 394px;
          .title {
            white-space: nowrap;
            padding: 25px 0px;
            background: rgba($color: #f5f5f5, $alpha: .93);
            font-size: 29.2px;
            color: #333333;
            position: absolute;
            left: 0;
            right: 0;
            text-align: center;
            box-sizing: border-box;
            height: 80px;
            bottom: 0;
          }
        }
        position: absolute;
        // left: 50%;
        // top: 50%;
        // transform: translate(-50%, -50%);
        overflow: hidden;
      }
      .grade {
        transform: scale(.8)
      }
      .video_cover {
        overflow: hidden;
        position: relative;
        width: 100%;
        height: 100%;
        img {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
        }
      }
      .title {
        font-size: 28px;
        line-height: 40px;
        padding: 13px 10px 4px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        width: 100%;
        min-width: 0;
      }
      &.active {
        position: relative;
        .content {
          position: absolute;
          left: 50%;
          top: 50%;
          z-index: 3;
          transform: translate(-50%, -50%) scale(1.2);
          -webkit-transform: translate(-50%, -50%) scale(1.2);
        }
      }
    }
  }
}
.swiperList {
  height: 700px;
  display: flex;
  align-items: center;
  background: #fff;
  box-sizing: border-box;
  padding-top: 70px;
  .swiper-slide {
    width: 433px;
    height: 534px;
    .content {
      width: 433px;
      height: 564px;
      border-radius: 10px;
      box-shadow: 4px 4px 12px 4px #DCDCDC;
      background: #fff;
      .content-top-left {
        flex: 1;
        min-width: 0;
        align-self: flex-start;
        .date {
          padding: 25px 10px 20px;
          font-size: 26px;
        }
        .date-time {
          font-size: 40px;
          font-weight: 900;
        }
        .remarks {
          font-size: 30px;
          padding: 20px 0;
        }
        .c-grade {
          font-size: 45px;
          margin-top: 70px;
          margin-bottom: 4px;
          color: #F4A82B;
        }
        .grade {
          transform: scale()
        }
      
      }
      .title-flow {
        padding: 10px;
        .button {
          border-radius: 22.5px;
          height: 45px;
          background-image: linear-gradient(-90deg, #F6CB2F 0%, #F39C6B 100%);
          box-sizing: border-box;
          font-size: 24px;
          width: 152px;
          line-height: 50px;
          color: #fff;
          text-align: center
        }
      }
      .leader {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        margin-bottom: 6px;
      }
      .leader, .actions {
        line-height: 30px;
        font-size: 22px;
        padding: 0 16px;
      }
      .content-top-right {
        width: 288px;
        height: 394px;
        .title {
          white-space: nowrap;
          padding: 25px 20px;
          overflow: hidden;
          text-overflow: ellipsis;
          background: rgba($color: #fff, $alpha: .6);
          font-size: 32px;
          color: #333333;
          position: absolute;
          left: 0;
          right: 0;
          text-align: center;
          box-sizing: border-box;
          height: 80px;
          bottom: 0;
        }
      }
      position: absolute;
      // left: 50%;
      // top: 50%;
      // transform: translate(-50%, -50%);
      box-shadow: 0 4px 20px#eee;
      overflow: hidden;
    }
    .grade {
      transform: scale(.8)
    }
    .video_cover {
      overflow: hidden;
      position: relative;
      width: 100%;
      height: 100%;
      img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
      }
    }
    .title {
      font-size: 28px;
      line-height: 40px;
      padding: 13px 10px 4px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      width: 100%;
      min-width: 0;
    }
    &.active {
      position: relative;
      .content {
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 3;
        transform: translate(-50%, -50%) scale(1.2);
        -webkit-transform: translate(-50%, -50%) scale(1.2);
      }
    }
  }
}
</style>



